
.commentInput {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 6;
  background-color: #fff;
  border-bottom: 1px #59b6d7 solid;
  border: none;
  padding: 10px 0;
}
.commentInput textarea {
    min-height: 34px !important;
}
.perComment {
  padding: 10px 0;
}
.perComment .profileLink {
    color: #333;
}
.commentCount {
  margin-bottom: .5em;
  font-size: 12px;
}
.wrapper {
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 5;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  position: fixed;
  overflow: auto;
  margin: 0;
}
.sendComment {
  font-size: 14px;
  padding: 3px !important;
  background-color: #59b6d7;
}
.sendComment[disabled] {
    background-color: #ccc !important;
    color: #fff !important;
}
.commentContent {
  font-size: 14px;
  color: #999;
}



